.alerta{
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error{
        background-color: $rojo;
    }
    &.exito{
        background-color: $verde;
    }
}
.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    align-items: center center;

    thead{
        background-color: $verde;

        th{
            color: $blanco;
            padding: 2rem;
        }
    }
    .imagen-tabla{
        width: 18rem;
    }
}
.imagen-small{
    width: 20rem;
    padding-bottom: 1rem;
}